import MenuIcon from '@mui/icons-material/Menu';
import SaveAltIcon from '@mui/icons-material/SaveAlt';
import NextIcon from '@mui/icons-material/ArrowForwardIos';
import './Header.css';

const Header = () => {
  return (
    <header className="header">
      <MenuIcon className="menu-icon" style={{ fontSize: '26px' }}/>
      <div className="text-container">
        <span className="geoMapper">GeoMapper</span>
        <span className="global" style={{ marginRight: '5px' }}>global</span>
        <NextIcon className="next-icon" style={{ fontSize: '14px' }}/>
      </div>
      <SaveAltIcon className="save-icon" style={{ fontSize: '26px' }}/>
    </header>
  );
};

export default Header;
